.b-footer-subscribe__title {
  margin-bottom: 5px;
  font-weight: bold; }

.b-footer-subscribe__note {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 12px;
  position: relative; }

.b-footer-subscribe__note-icon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: none; }

.b-footer-subscribe__note-icon .icon-svg {
  font-size: 22px; }

.b-footer-subscribe__form {
  margin-top: 10px;
  margin-bottom: 10px; }

.b-footer-subscribe__form .input-group-btn {
  position: relative; }

.b-footer-subscribe__form .input-group .form-control:focus {
  box-shadow: none;
  border-color: transparent;
  z-index: 2; }

.b-footer-subscribe__form .icon-svg {
  position: absolute;
  z-index: 3;
  font-size: 20px;
  left: -1px;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin: auto;
  cursor: pointer; }

.b-footer-subscribe__form .icon-svg:hover + .btn {
  color: #fff;
  background-color: #13161c;
  border-color: #13161c; }

.b-footer-subscribe .btn {
  min-width: 40px;
  height: 40px; }

@media (min-width: 992px) {
  .b-footer-subscribe__note-icon {
    display: block; }
  .b-footer-subscribe__note {
    padding-left: 35px; } }
